import {getBalance, getBill} from '../../api/api.js';
	export default {
		data() {
			return {
				assetInfo: '',
				listInfo: '',
				pageCounter:'10',
				isShow:true
			}
		},
		methods: {
			getBalanceData() {
				getBalance().then(data => {
					this.assetInfo = data;
				});
			},
			getBillData() {
				getBill().then(data => {
					// console.log(data)
					this.listInfo = data.slice(0,this.pageCounter);
					
					console.log(data.slice(0,this.pageCounter));
				});
			},
			loadMore() {
				getBill().then(data => {
					// console.log(data)
					this.listInfo = this.listInfo.concat(data.slice(this.pageCounter))
				});
				this.isShow = false
			}
		},
		onLoad() {
			this.getBalanceData();
			this.getBillData();
		}
	}
